.page-header{
    display: flex;
    flex-direction: column;
    background-color: var(--color-primary);
}

.page-header .top-bar-container{
    width: 90%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--color-text-in-primary);
    padding: 1.6rem;
}

/*Trata o Link como "a" normalmente no CSS*/
.page-header .top-bar-container a{
    height: 1.6rem;
    transition: opacity 0.2s;
}

.page-header .top-bar-container a:hover{
    opacity: 0.6;
}

/*Pega a imagem que está no mesmo nivel que link, por exempo, ou seja que está apenas no
primeiro nível do .top-bar-container*/
.page-header .top-bar-container > img{
    height: 5.2rem;
}

.page-header .header-content{
    width: 90%;
    margin: 0 auto;
    position: relative;
    margin: 3.2rem auto;
}

.page-header .header-content strong{
    /*O 700 deixa a fonte em negrito*/
    font: 700 3.6rem Poppins;
    line-height: 4.2rem;
    color: var(--color-title-in-primary);
}

.page-header .header-content p{
    /* Quebrar a linha */
    max-width: 30rem;
    font-size: 1.6rem;
    line-height: 2.6rem;
    color: var(--color-text-in-primary);
    margin-top: 2.4rem;
}

@media(min-width: 700px){
    .page-header{
        height: 340px;
    }

    .page-header .top-bar-container{
        max-width: 1100px;
    }

    .page-header .header-content{
        flex: 1;
        max-width: 740px;
        margin: 0 auto;
        padding-bottom: 48px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
    }

    .page-header .header-content strong {
        /* Poder quebrar a linha do strong */
        max-width: 350px;
        
    }

}